<template>
    <div ref="wrapper">
      <div ref="content">
        <slot></slot>
      </div>
    </div>
</template>

<script>
  import BScroll from 'better-scroll'
  export default {
    name: "Scroll",
    props:{
      probeTypeNum:{
        type:Number,
        default: 1
      },
      pullUpLoad:{
        type: Boolean,
        default: false,
      }
    },
    data() {
      return {
        scroll: null
      };
    },
    mounted(){
      this.scroll = new BScroll(this.$refs.wrapper,{
        // scroll
        click: true,
        probeType: this.probeTypeNum,
        pullUpLoad: this.pullUpLoad,
      });

      // 
      this.scroll.on('scroll',(position) => {
        this.$emit('scroll',position);
      });

      // 
      this.scroll.on('pullingUp',()=>{
        this.$emit('loadMore');
      });
    },
    methods: {
      // better-scroll
      goTop(x,y,time=500){
        this.scroll.scrollTo(x,y,time);
      },
      // 
      refresh(){
        this.scroll.refresh();
      }
    },
    components: {},
  }
</script>

